/* IMPORT GENERAL */
import {win, doc, log, al, addEv, que, queAll, gId} from "./general/var.js"

/* IMPORT PARTIALS */
import { customPropertiesPolyfill } from './partials/customPropertiesPolyfill.js'
//import { openLoader } from './partials/loader.js'
import { alerts } from './partials/alerts.js'
import { menu } from './partials/menu.js'
import { modal } from './partials/modal.js'
import 'regenerator-runtime/runtime'

/* ON LOAD */
win.onload = () => doc.que('.loader').classList.add('hide')


// /* LOAD COMPLETE */
doc.addEv('DOMContentLoaded', e => {
	var divisor = doc.que(".divisor"), divisor2 = doc.que(".divisor2"), divisor3 = doc.que(".divisor3"),
    handle = doc.que(".handle"),
    slider = doc.que(".slider");
	if(slider){
		slider.addEventListener ("input", function (e){
			handle.style.left = slider.value+"%";
			
			if(divisor){
				divisor.style.width = slider.value+"%";
			}
			if(divisor2){
				divisor2.style.width = slider.value+"%";
			}
			if(divisor3){
				divisor3.style.width = slider.value+"%";
			}
		});	
	}


	// CUSTOM PROPERTIES POLYFILL
	customPropertiesPolyfill()

	// OPEN LOADER
	//openLoader() 

	// ALERTS
	// alerts()

	// MENU
	menu()

	// MODAL
	modal()

	// MINI TAB
	const miniTab = doc.que('.miniTab')

	function hideTabs(miniTab){
		const itemsActive = miniTab.queAll('.miniTab__item.active'),
			contentsActive = miniTab.queAll('.miniTab__content.active')

		Array.from(itemsActive).map( itemAc => itemAc.classList.remove('active'))
		Array.from(contentsActive).map( itemAc => itemAc.classList.remove('active'))

	}

	function showTab(dataIt){
		const content = miniTab.que(`.miniTab__content[data-miniTab="${dataIt}"]`),
			item = miniTab.que(`.miniTab__item[data-miniTab="${dataIt}"]`),
			allElements = [content, item]

		allElements.forEach( el => {
			el.classList.add('active')
		} )

	}

	function changeTab(dataIt, miniTab){
		hideTabs(miniTab)
		showTab(dataIt)
	}

	if( miniTab ){
		const miniTabItem = doc.queAll('.miniTab__item')

		Array.from(miniTabItem).map( item => {
			item.addEv('click', e => {
				e.preventDefault()
				let dataItem = item.getAttribute('data-miniTab')
				changeTab(dataItem, miniTab)
			})
		} )
	}

	//CAROUSEL

	const depreText = doc.que('#depreText'),
		depreImg = doc.que('#depreImg'),
		esquiText = doc.que('#esquiText'),
		esquiImg = doc.que('#esquiImg'),
		simpleCarousel = doc.que('.carousel--simple')

	
	if ($('.owl-carousel').length > 0) {
		/*
        var height = $(window).height();

        $('.owl-carousel').height(height);
        */
	   	if( $('.owl-carousel').hasClass('carousel--simple') ) {
			$('.carousel--simple').owlCarousel({
				items: 1,
				loop: false,
				rewind: true,
				margin: 0,
				center: true,
				nav: true,
				dots: false
			})
		}
	   	if( $('.owl-carousel').hasClass('carousel--map') ) {
			$('.carousel--map').owlCarousel({
				items: 1,
				loop: false,
				rewind: true,
				margin:0,
				// autoplay: true,
				center:true,
				nav: true,
				dots: false
			});
		}
        if ($('.owl-carousel').hasClass('carousel--depre')) {
            $(depreText).owlCarousel({
                items: 1,
				loop: false,
				rewind: true,
                margin: 1,
                // autoplay: true,
                center:true,
				nav: true,
				navSpeed:200,
				dots: false
            });
		}
		if ($('.owl-carousel').hasClass('carousel--depreImg')) {
            $(depreImg).owlCarousel({
                items: 1,
				loop: false,
				rewind: true,
                margin: 1,
                // autoplay: true,
                center:true,
                nav: false,
                dots: false
            });
		}
		//Sync o2 by o1
		var o1 = $('#depreText'),
  			o2 = $('#depreImg');

		o1.on('change.owl.carousel', function(event) {
			o2.trigger('to.owl.carousel', [event.item.index + 1,300,true]);
		})
		
		if ($('.owl-carousel').hasClass('carousel--esqui')) {
            $(esquiText).owlCarousel({
                items: 1,
                loop: true,
                margin:0,
                // autoplay: true,
                center:true,
                nav: true,
                dots: false
            });
		}
		if ($('.owl-carousel').hasClass('carousel--esquiImg')) {
            $(esquiImg).owlCarousel({
                items: 1,
				loop: false,
				rewind: true,
                margin:0,
                // autoplay: true,
                center:true,
                nav: false,
                dots: false
            });
		}
		//Sync p2 by p1
		var p1 = $('#esquiText'),
  			p2 = $('#esquiImg');

		p1.on('change.owl.carousel', function(event) {
			p2.trigger('to.owl.carousel', [event.item.index + 1,300,true]);
		})
	}
	const nav = doc.que('.owl-carousel .owl-nav') 
	if(nav){
		nav.classList.remove('disabled')
	}

	//Link Poltica de privacidad y Aviso
	const linkPoli = doc.que('#linkPoli'), linkAviso = doc.que('#linkAviso');
	if(linkPoli){
		linkPoli.addEventListener('click',function (e){
			window.open('resourses/Poltica de Privacidad 1.pdf', '_blank');
		})
	}
	if(linkAviso){
		linkAviso.addEventListener('click',function (e){
			window.open('resourses/Aviso Legal 1.pdf', '_blank');
		})
	}

	//Link Social

	const Lface = doc.que('#Lface')
	if(Lface){
		Lface.addEventListener('click',function(e){
			e.preventDefault()
			window.open('https://www.facebook.com/DerrotemosLosEstigmas/', '_blank');
		})
	}
	const Ligm = doc.que('#Ligm')
	if(Ligm){
		Ligm.addEventListener('click',function(e){
			e.preventDefault()
			window.open('https://instagram.com/derrotemoslosestigmas?igshid=919d8jsyjbps', '_blank');
		})
	}

	//Referencias Home
	function linkRef (element, value){

		element.onclick = function(e){
			e.preventDefault()
			window.open(value, '_blank');	
		}
	}

	const ref = doc.queAll('.subH')
	var links = Array.from(ref)
	var sitePath = window.location.pathname
	if(sitePath == "/"){
		linkRef(links[0],"https://www.who.int/es/news-room/fact-sheets/detail/mental-health-strengthening-our-response")
		linkRef(links[1],"https://www.who.int/mental_health/publications/mental_disorders_LAC/es/#:~:text=Como%20se%20destaca%20en%20este,las%20etapas%20de%20la%20vida.")
		linkRef(links[2],"https://www.paho.org/hq/index.php?option=com_content&view=article&id=13102:depression-lets-talk-says-who-as-depression-tops-list-of-causes-of-ill-health&Itemid=1926&lang=es")
		linkRef(links[3],"https://iris.paho.org/bitstream/handle/10665.2/34006/PAHONMH17005-spa.pdf?sequence=1&isAllowed=y")	
	}

	//Referencias Depresin

	if(sitePath == "/depresion.html"){
		linkRef(links[0],"https://www.who.int/es/news-room/fact-sheets/detail/depression")
		linkRef(links[1],"https://www.janssen.com/es/neuroscience/four-things-we-know-about-treatment-resistant-depression")
		linkRef(links[2],"https://www.who.int/es/news-room/fact-sheets/detail/depression")
		linkRef(links[3],"https://www.jnj.com/janssen-seeks-expanded-use-of-spravato-esketamine-nasal-spray-in-europe-as-a-treatment-for-depressive-symptoms-in-adults-with-major-depressive-disorder-who-have-current-suicidal-ideation-with-intent")
		linkRef(links[4],"https://www.nimh.nih.gov/health/topics/depression/index.shtml")
		linkRef(links[5],"https://www.janssen.com/es/neuroscience/four-things-we-know-about-treatment-resistant-depression")
		linkRef(links[6],"https://www.jnj.com/janssen-seeks-expanded-use-of-spravato-esketamine-nasal-spray-in-europe-as-a-treatment-for-depressive-symptoms-in-adults-with-major-depressive-disorder-who-have-current-suicidal-ideation-with-intent")
		linkRef(links[7],"https://iris.paho.org/bitstream/handle/10665.2/34006/PAHONMH17005-spa.pdf?sequence=1&isAllowed=y")	
	}

	//Referencias Esquizofrenia
	
	if(sitePath == "/esquizofrenia.html"){
		linkRef(links[0],"https://www.who.int/es/news-room/fact-sheets/detail/schizophrenia")
		linkRef(links[1],"https://www.janssen.com/bolivia/educacion-en-salud/esquizofrenia")
		linkRef(links[2],"https://www.janssen.com/bolivia/educacion-en-salud/esquizofrenia")
		linkRef(links[3],"https://www.semanticscholar.org/paper/Relapse-duration%2C-treatment-intensity%2C-and-brain-in-Andreasen-Liu/6dea880d9def60ef391869d6e7171acc6034da27?p2d")
		linkRef(links[4],"https://www.janssen.com/bolivia/educacion-en-salud/esquizofrenia")	
		linkRef(links[5],"http://www.apef.org.ar/esquizofrenia")	
	}
	
	var card = doc.queAll('.depreBox__gallery__item');
	Array.from(card).map(card =>{
		card.addEventListener( 'click', function() {
			card.classList.toggle('is-flipped');
		});
	})

	var cardEsq = doc.queAll('.esquiBox__gallery__item');
	Array.from(cardEsq).map(card =>{
		card.addEventListener( 'click', function() {
			card.classList.toggle('is-flipped');
		});
	})
})